.slider {
  width: 33.33%;
  flex-wrap: wrap;
  justify-content: center;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.slider img {
  width: 85px;
  height: 85px;
  border-radius: 50%;
  animation-name: scaleImg;
  animation-duration: 6s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes scaleImg {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1);
  }
}

#client1:checked ~ .s1 {
  margin-left: 0;
}

#client2:checked ~ .s1 {
  transform: translateX(100%);
}

#client2:checked ~ .s2 {
  margin-left: -33.33%;
}

#client3:checked ~ .s3 {
  margin-left: -66.66%;
}

#client3:checked ~ .s1 {
  transform: translateX(100%);
}

