.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  background: rgba(80, 77, 77, 0.5);
  transition: opacity 300ms linear, visibility 300ms linear;
}

.our-location__list{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.our-location {
  position: absolute;
  width: 280px;
  height: 365px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform 300ms linear;
  background: var(--primary_text-color);
  border-radius: 10px;
  padding: 28px;
  @include respond-min('tablets') {
    width: 568px;
    height: 458px;
  }
}

.our-location__title {
  font-family: var(--secondary-font);
  color: var(--secondary-black-color);
  @include font(16, 400, 24, 0);
  text-align: center;
  margin-bottom: 20px;
  @include respond-min('tablets') {
    @include font(20, 400, 28, 0);
    margin-bottom: 32px;
  }
}
.our-location__map {
  border-radius: 10px;
  overflow: hidden;
  margin: 0 auto;
  width: 224px;
  height: 200px;
  margin-bottom: 24px;
  @include respond-min('tablets') {
    width: 448px;
    height: 260px;
    margin-bottom: 32px;
  }
}

.our-location__link{
background-color: var(--dark-yellow-bg-color);
    width: 134px;
    height: 42px;
   border-radius: 10px;
        @include font(12, 600, 15, 0);
padding: 12px 34px ;
   @include respond-min('tablets') {
    @include font(14, 600, 17, 0);
    width: 145px;
    height: 42px;
    margin-bottom: 32px;}
    & a{
 color: var(--secondary-black-color);
    }
}
.btn-close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  fill: var(--secondary-black-color);
  background: var(--primary-white-color);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: linear;
  transition-delay: 0;
  &:hover {
    fill: var(--accent_color);
  }
}