.section-about {
    background-color: var(--primari-light-bg-color);
}

.section-about__image {
    margin-right: auto;
    margin-left: auto;

    @media screen and (min-width: 768px) {
        height: 340px;
        width: 340px;
    }

    @media screen and (min-width: 1200px) {
        height: 491px;
        width: 529px;
    }
}

.section-about__label {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 800;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--secondary-bg-color);
    padding-top: 60px;

    @media screen and (min-width: 480px) {
            font-size: 22px;
            line-height: 1.15;
            margin-bottom: 16px;
        }

    @media screen and (min-width: 768px) {
        font-size: 26px;
        line-height: 1.15;
        margin-bottom: 16px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 30px;
        line-height: 1.13;
        margin-bottom: 15px;
    }
}

.section-about__title {
    max-width: 200px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
 
    font-family: 'Seymour One';
    font-style: normal;
    font-size: 34px;
    line-height: 43px;
    letter-spacing: 0.06em;
    text-transform: uppercase;

    padding-left: 9px;
    margin-bottom: 26px;

    @media screen and (min-width: 768px) {
        max-width: 665px;
        font-size: 48px;
        line-height: 1.15;
        margin-bottom: 50px;
    }

    @media screen and (min-width: 1200px) {
        max-width: 790px;
        font-size: 58px;
        line-height: 1.14;
        margin-bottom: 55px;
    }
}

.section-about__main {
    @media screen and (min-width: 768px) {
        display: flex;
    }

    @media screen and (min-width: 1200px) {
        display: flex;
    }
}

.section-about__body {
    margin-right: auto;
    margin-left: auto;
    padding-right: 30px;
    max-width: 320px;

    @media screen and (min-width: 768px) {
        display: block;
        padding: 0;
        margin-left: 17px;
        width: 340px;
    }

    @media screen and (min-width: 1200px) {
        display: block;
        padding: 51px 0 0;
        margin-left: 74px;
        width: 463px;
    }
}

.section-about__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-size: 14px;
    line-height: 194%;
    letter-spacing: 0.04em;
    text-align: center;
     color: var(--secondary-bg-color);
    margin-top: 46px;
    margin-bottom: 27px;
    font-weight: 700;
    outline: transparent;
    width: 220px;
    margin-left: auto;

    @media screen and (min-width: 480px) {
           
        font-size: 14px;
        line-height: 1.79;
        margin-top: 50px;
        margin-bottom: 19px;
        
        }

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.79;
        margin-top: 50px;
        margin-bottom: 19px;
        text-align: left;
        width: 400px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.75;
        margin-top: 4px;
        margin-bottom: 35px;
    }
}

// Кнопка модального окна OPEN
.section-about__button {
    color: var(--secondary-black-color);
    background-color: var(--dark-yellow-bg-color);
    display: block;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 164px;
    height: 42px;
    font-size: 14px;
    line-height: 1.29;
    border: 2px solid #0000;
    border-radius: 10px;
    font-weight: 700;
    font-style: normal;
    cursor: pointer;
    transition: transform 250ms ease, border 200ms ease, color 200ms ease;
    outline: transparent;

    &:focus {
        transform: scale(1.1);
        background-color: var(--dark-green-bg-color);
        color: var(--primary_text-color);
    }
    &:hover{
            transform: scale(1.1);
            background-color: var(--dark-green-bg-color);
        }


    @media screen and (min-width: 480px) {
        font-size: 14px;
        line-height: 1.29;
    }

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 1.31;
    }
}

.section-about__icon {
    border: none;
    vertical-align: middle;
    margin-left: 13.6px;
    border-radius: 2px;
    fill: currentColor;

    @media screen and (min-width: 768px) {
        width: 9px;
        height: 12px;
    }

    @media screen and (min-width: 1200px) {
        width: 10px;
        height: 15px;
    }
}